import {useGoogleLogin, GoogleLogin, useGoogleOneTapLogin} from "@react-oauth/google"
import axios from "axios"

const Signup = () => {

    const handleSignUp = useGoogleLogin({
          onSuccess: async tokenResponse => {
            try{
            // fetching userinfo can be done on the client or the server
            const userInfo = await axios
              .get('https://www.googleapis.com/oauth2/v3/userinfo', {
                headers: { Authorization: `Bearer ${tokenResponse.access_token}` },
              })
              .then(res => res.data);
              
            if(userInfo){

                  await axios
                    .post("https://priconne-backend.onrender.com/auth/google/refresh-token", {
                        userInfo,
                        tokenResponse             
                    })
                    .then(response=>{
                      console.log(response);
                      localStorage.setItem("mongoID", response.data.user._id);
                      localStorage.setItem("id", userInfo.sub);
                      window.location.reload();
                    });
              };
      
            }catch(err){
              console.error(err);
            }
          },
          referrerPolicy: "no-referrer",
          onFailure: (errorResponse) => console.error(errorResponse),
          clientId: process.env.REACT_APP_GOOGLE_CLIENT_ID,
          cookiePolicy: "single_host_origin",
          responseType: "code,token",
          flow: 'implicit',
          onError: errorResponse => console.error(errorResponse),  
    });

    const GoogleLoginButton = () => {
      return (
        <GoogleLogin
          clientId={process.env.REACT_APP_GOOGLE_CLIENT_ID}
          onSuccess={handleSignUp}
          onFailure={handleSignUp}
          referrerPolicy={"no-referrer"}
          cookiePolicy={"single_host_origin"}
          responseType={"code,token"}
          render={(renderProps) => <useGoogleOneTapSignIn onClick={renderProps.onClick} disabled={renderProps.disabled} />}
        />
      );
    };

    return(
      <GoogleLoginButton/>

    )

}

export default Signup